import {Carousel,WingBlank} from 'antd-mobile';
import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import './tao.css'
//商品页
//所有的商品的列表
//import jiubeipic from './Image/jiubei.png'
import Boxcommodity from './box-commodity.js'
const Commodity = (props) => {  
	var list = props.data.datalist;
	return (<div>
		<ul className="box">
		{
			list.map((item,idx)=>(
				<Boxcommodity data={item} key={idx}/>
			))
		}
		</ul>
		
	</div>)
}

export default Commodity;


