import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './tao.css'
import store from '../store/store.js'
import {shoucangapi} from '../store/actionCreators.js'
import {loginapi} from '../store/actionCreators.js'
import { connect } from 'react-redux';

//详情页下面的按钮
class Ctab extends React.Component {
	
    constructor(props) {
        super(props);
		//console.log(props)
        this.state = {
            selectedTab: '',
			alldata: props.data,
			p: props
        };
    }
	starGood = () => {
	    // 登录验证
	    if (!this.props.userinfo) {
			//console.log('需要登录');
			this.props.dispatch(loginapi());
			this.props.history.push('/login')
			//console.log('登录成功');
			
	    }
		var id = this.state.alldata.id;
		var flag = 0;
		var shoulist = this.state.p.shoucang;
		//避免重复添加收藏商品
		for(var i = 0; i < shoulist.length; i++) {
			if(id === shoulist[i].id) {
				//console.log('此商品已经被收藏了...');
				flag = 1;
				break;
			}
		}
		if(!flag) {
			//console.log('收藏成功！');
			this.state.p.dispatch(shoucangapi(this.state.alldata));
		}
		//console.log(shoulist)
	}
		
    render() {
		//console.log(this.state.p)
        const { pathname } = this.props.location;
		//console.log(pathname);
        return (
            <div style={ {   
				position: 'fixed',  
				width: '100%',  
				bottom: 0 } }
			>
            <TabBar 
				unselectedTintColor="#949494" 
				tintColor="red" 
				barTintColor="white" 
			>
				
                    <TabBar.Item
                        title="店铺"
                        key=""
                        icon={
                            <i className='iconfont icon-dianpu'></i>                           
                        }
                        selected={ pathname === '' }
                    > </TabBar.Item>
					
                    <TabBar.Item
						title="客服"
						key=""
                        icon={
                            <i className='iconfont icon-kefu'></i>                           
                        }
                        selected={ pathname === '' }
                    ></TabBar.Item>
					
                    <TabBar.Item
                        title="收藏"
                        key="shoucang2"
                        icon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>                           
                        }
						selected={this.state.selectedTab === 'blueTab'}						//selected={this.state.stared}
						onPress={() => {
							this.starGood();
							this.setState({
								selectedTab: 'blueTab',
							});
						}}
                    > </TabBar.Item>
					
					
                    <TabBar.Item 
                        icon={<i className='iconfont ' ></i> }
						selectedIcon={
						    <i className='iconfont'></i>                           
						}
                        title="加入购物车"
                        key=""
                        selected={ pathname === '' }
                    ></TabBar.Item>
					
					<TabBar.Item
					    icon={<i className='iconfont'></i> }
						selectedIcon={
						    <i className='iconfont'></i>                           
						}
					    title="立即购买"
					    key=""
					    selected={ pathname === '' }
					></TabBar.Item>
				</TabBar>
            </div>
        );
    }
}
//export default withRouter(Ctab);
export default connect((store) => {
	return store;
})(withRouter(Ctab))