import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import {Button, TabBar } from 'antd-mobile';
import Mytab from './Mytab'
import Dingdan from './Dingdan'
import Shoucang from './Shoucang'
import My from './My'
import Shouye from './Shouye'
import Search from './child/Search'
import Listdetail from './Listdetail.js'
import Boxcommodity from './box-commodity.js'
import login from './login.js'
import store from '../store/store.js'
import { Provider } from 'react-redux'


const App = () => {
	return(
	<Provider store={store}>
		<Router>
			<Switch>
				<Route path='/Shouye' component={Shouye} />
				<Route path='/Shoucang' component={Shoucang} />
				<Route path='/Dingdan' component={Dingdan} />
				<Route path='/My' component={My} />
				<Route path='/Search' component={Search} />
				<Route path='/Listdetail' component={Listdetail} />
				<Route path='/Boxcommodity' component={Boxcommodity} />
				<Route path='/login' component={login} />
				{/* <Route component={Home} /> */}
				<Route render={()=><Redirect to='/Shouye'/>} />
			</Switch>
			
			<Mytab />
			
		</Router>
	</Provider>)
}
export default App;