let store = {
	datalist:[],
	shoucang:[],
	userinfo:''
}
function list(state=store, action){
    if(action.type=='LOGIN'){
        //console.log('reducer',action);
		return Object.assign({},state,{userinfo:action.username})
        //return [...state.userinfo, action.username]
    }
    else if(action.type=='GOODAPI'){
		return Object.assign({},state,{datalist:action.data})
    }
	else if(action.type=='SHOUAPI'){
		return Object.assign({},state,{shoucang:[...state.shoucang,action.alldata]})
	}
	else if(action.type=='DELSHOU'){
		var id = action.alldata.item.id;
		//console.log('将要删除id是'+id+'的内容');
		for(var i = 0; i < state.shoucang.length; i++) {
			if(state.shoucang[i].id === id) {
				state.shoucang.splice(i, 1);
				break
			}
		}
		return Object.assign(state)
		//return shoucang;
		//return Object.assign({},state,{shoucang:[...state.shoucang,action.alldata]})
	}
    return state;
}
export default list;