//登录
const loginapi = (username)=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url, {
            method: 'get',
            headers: {
                'Content-Type': 'application/json'
            },
        })
        .then(res => res.json())
        .then(res => {
            dispatch({
                type: 'LOGIN',
                username: res.data.name
            })
        })
    }
}
//所有商品的
const goodapi = ()=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, {
            method: 'get',
            headers: {
                'Content-Type': 'application/json'
            },
        })
        .then(res => res.json())
        .then(res => {
            dispatch({
                type: 'GOODAPI',
                data: res.data
            })
        })
    }
}
//收藏的商品
const shoucangapi = (alldata)=>{
	//console.log('shoucangapi传入的alldata：');
	//console.log(alldata);
    return {
        type: 'SHOUAPI',
        alldata: alldata
    }
}
//取消收藏商品
const delshou = (alldata)=>{
	//console.log('delshou要删除的的alldata：');
	//console.log(alldata);
    return {
        type: 'DELSHOU',
        alldata: alldata
    }
}
export  {loginapi, goodapi, shoucangapi, delshou}

