import React from 'react'
import Boxcommodity from './box-commodity.js'
import { connect } from 'react-redux';
import {delshou} from '../store/actionCreators.js'
import {loginapi} from '../store/actionCreators.js'

const Shoucang = (props) => {  
	var list = props.shoucang;
	//判断是否需要登录
	if (!props.userinfo) {
		console.log('正在登录...请稍稍等...');
		props.history.push('/login')
		props.dispatch(loginapi());
		//props.history.push('/Shoucang');
	}
	return (<div>
		<ul className="">
		{
			list.map((item,idx)=>(
				<div style={{border:'1px solid #000'}} key={idx}>
					<h3>商品序号:{idx+1}</h3>
					<h1>{item.title}</h1>
					<img src={item.img} alt='' style={{width:'100px',height:'100px'}}/>,
					<button onClick={() => {
						props.dispatch(delshou({item}));
						props.history.push('/Shoucang');
					}}>取消收藏</button>
				</div>
			))
		}
		</ul>
		<div style={{height:'200px',backgroundColor:''}}></div>
	</div>)
	
}
export default connect((store) => {
	return store;
})(Shoucang)