import React from 'react'
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './tao.css'
import Ctab from './Ctab.js'

//点进首页中任何一个商品后，跳转到此页面的详情页
class Listdetail extends React.Component {
	constructor(props) {
		var data = props.history.location.state.data;
	    super(props);
	    this.state = {
	        url: data.img,
			price: data.price,
			id: data.id,
			alldata:data
	    };
	}
	render() {
		return (<div style={{height:'800px'}}>
			<img src={this.state.url} style={{width:'100%'}}/>
			<button className="left-top-fanhui" onClick={() => this.props.history.goBack()}> &lt; </button>
			<h2>
				${this.state.price}
			</h2>
			{/*引入底部组件*/}
			<Ctab data={this.state.alldata}/>
		</div>)
	}
}

export default Listdetail
