import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react';

//首页的轮播图
class Lunbo extends React.Component {
  state = {
    data: [],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://gw.alicdn.com/imgextra/i1/18/O1CN01y0npFM1C0HlEx6dCY_!!18-0-ppp-picassogw.jpg',
				'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
				'https://gw.alicdn.com/imgextra/i3/124/O1CN01DWedGM1Cmpk9TGU8s_!!124-0-lubanu.jpg',
				'https://gw.alicdn.com/imgextra/i4/116/O1CN01V059ZP1CjAZiwVFd1_!!116-0-lubanu.jpg',
				'https://gw.alicdn.com/imgextra/i4/158/O1CN01PXt8FO1D2PD1kFyUe_!!158-0-lubanu.jpg'],
		flag:true
      });
    },2000);
  }
  render() {
    return (
        <Carousel
          autoplay={true}
          infinite
		  autoplayInterval={2000}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="#"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
				src={`${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
    );
  }
}

//ReactDOM.render(<App />, mountNode);
export default Lunbo;