import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import {Button, TabBar } from 'antd-mobile';
import './tao.css'
//首页中，每一个商品的信息
const Boxcommodity = (props) => {
	var data = props.data;
    return (
			<NavLink to={{pathname:'/Listdetail',state:{data:data}}}>
				<li className="shang-box" >
					<img src={data.img} style={{width:'100%'}} alt=""/>
					<div className="box-text"> 
						<div className="box-miaoshu">
							{data.title}
						</div> <br />
						￥{data.price} &nbsp;&nbsp; 
						{data.id}人已购买 
					</div>
				</li>
			</NavLink>
    )
}

export default Boxcommodity
